const Util = {
  getAllPermissions () {
    let permissions = JSON.parse('[{"name":"","code":"A"},{"name":"","code":"AA"},{"name":"","code":"AB"},{"name":"","code":"AC"},{"name":"","code":"AD"},{"name":"","code":"AE"},{"name":"","code":"AEA"},{"name":"","code":"AEB"},{"name":"","code":"AEC"},' +
    '{"name":"","code":"B"},{"name":"","code":"BA"},{"name":"","code":"BB"},{"name":"","code":"BC"},{"name":"","code":"BD"},{"name":"","code":"BE"},' +
    '{"name":"","code":"C"},{"name":"","code":"CA"},{"name":"","code":"CB"},{"name":"","code":"CC"},{"name":"","code":"CD"},' +
    '{"name":"","code":"D"},{"name":"","code":"DA"},{"name":"","code":"DAA"},{"name":"","code":"DAB"},{"name":"","code":"DAC"},{"name":"","code":"DAD"},{"name":"","code":"DB"},{"name":"","code":"DBA"},{"name":"","code":"DBB"},{"name":"","code":"DBC"},' +
    '{"name":"","code":"E"},{"name":"","code":"EA"},{"name":"","code":"EB"},{"name":"","code":"EC"},' +
    '{"name":"","code":"F"},{"name":"","code":"FA"},{"name":"Blog","code":"FB"},{"name":"Blog","code":"FBA"},{"name":"Blog","code":"FBB"},{"name":"","code":"FC"},{"name":"","code":"FCA"},' +
    '{"name":"","code":"G"},{"name":"","code":"GA"},{"name":"","code":"GB"},{"name":"","code":"GC"},{"name":"","code":"GD"},{"name":"Messagenger","code":"GE"}]')
    /**
     * 
     */
    let permissionsServerLeverNot1 = JSON.parse('[{"name":"","code":"A"},{"name":"","code":"AA"},{"name":"","code":"AB"},{"name":"","code":"AC"},{"name":"","code":"AD"},{"name":"","code":"AE"},{"name":"","code":"AEA"},{"name":"","code":"AEB"},{"name":"","code":"AEC"},' +
    '{"name":"","code":"B"},{"name":"","code":"BA"},{"name":"","code":"BB"},{"name":"","code":"BC"},{"name":"","code":"BD"},{"name":"","code":"BE"},' +
    '{"name":"","code":"C"},{"name":"","code":"CA"},{"name":"","code":"CB"},{"name":"","code":"CC"},' +
    '{"name":"","code":"D"},{"name":"","code":"DA"},{"name":"","code":"DAA"},{"name":"","code":"DAB"},{"name":"","code":"DAC"},{"name":"","code":"DAD"},{"name":"","code":"DB"},{"name":"","code":"DBA"},{"name":"","code":"DBB"},{"name":"","code":"DBC"},' +
    '{"name":"","code":"E"},{"name":"","code":"EA"},{"name":"","code":"EB"},{"name":"","code":"EC"},' +
    '{"name":"","code":"FA"},{"name":"Blog","code":"FB"},{"name":"Blog","code":"FBA"},{"name":"Blog","code":"FBB"},{"name":"","code":"FC"},{"name":"","code":"FCA"},' +
    '{"name":"","code":"G"},{"name":"","code":"GA"},{"name":"","code":"GB"},{"name":"","code":"GC"},{"name":"","code":"GD"},{"name":"Messagenger","code":"GE"}]')
    return window.localStorage.serverLevel === '1' ? permissions : permissionsServerLeverNot1
  },
  checkUrl: function (url) {
    if (url) {
      if (url.substr(0, 8).toLowerCase() !== 'https://') {
        return url.substr(0, 7).toLowerCase() === 'http://' ? url : 'http://' + url
      } else {
        return url
      }
    } else {
      return ''
    }
  },
   /**
   * 
   * @param  {[type]}  thisRight  [description]
   * @param  {[type]}  rightArray [description]
   * @return {Boolean}            [description]
   */
  hasRightByCode: function (code) {
    let rightArray = JSON.parse(sessionStorage.functions)
    let isIn = rightArray.some(function (it) {
      return it.code === code
    })
    /**
     *  thisRight.code   code
     * @param  {[type]} thisRight.code [description]
     * @return {[type]}                [description]
     */
    if (code === '') {
      return true
    } else {
      return isIn
    }
  }
}
export default Util



